import axios from 'axios';
import { Message } from 'element-ui';
let localhosts = 'http://www.xxx.com';   //
axios.interceptors.request.use(config => {//(token)
    return config
},error =>{
    Message.error(error)
    return Promise.reject(error);
});
axios.interceptors.response.use(response => {//
    return response.data
},error => {
    Message.error('Http');
    return Promise.reject(error.response);;
});
function successfun(res){//200
    if(res.code === 200){
        return res
    }else{        
         Message.warning(res.message);
         return res;
    } 
}
function errorfun(res){
    if(res.code != 200){
        Message.error(res.message);
        return res;
    }
}
export default{
    post(url,data){//post
        return axios({
            method:'post',
            baseURL:localhosts,
            url,
            data: data,
            withCredentials: true,
            timeout:5000,//
        }).then(res => {
            return successfun(res)
        },err => {
            return errorfun(err);
        })
    },
    get(url,params){//get
        return axios({
            method:'get',
            baseURL:localhosts,
            url,
            params,
            withCredentials: true,
            timeout:5000,
        }).then(res => {
            return successfun(res)
        },err => {
            return errorfun(err)
        })
    }
    //(delete,put)...
}